export default "<template>\r\n  <nm-box-row style=\"height:400px\">\r\n    <nm-box-col :span=\"12\">\r\n      <nm-form-page v-bind=\"form\">\r\n        <el-form-item label=\"城市：\" prop=\"area\">\r\n          <nm-select v-model=\"form.model.area\" v-bind=\"select\"/>\r\n        </el-form-item>\r\n        <el-form-item label=\"城市：\" prop=\"area1\">\r\n          <nm-select v-model=\"form.model.area1\" disabled v-bind=\"select\"/>\r\n        </el-form-item>\r\n      </nm-form-page>\r\n    </nm-box-col>\r\n    <nm-box-col :span=\"12\"></nm-box-col>\r\n  </nm-box-row>\r\n</template>\r\n<script>\r\nexport default {\r\n  data () {\r\n    return {\r\n      form: {\r\n        title: '简单下拉列表',\r\n        icon: 'list',\r\n        model: {\r\n          area: '',\r\n          area1: ''\r\n        },\r\n        action: this.submit\r\n      },\r\n      select: {\r\n        method: this.queryOptions\r\n      }\r\n    }\r\n  },\r\n  methods: {\r\n    queryOptions () {\r\n      return new Promise(resolve => {\r\n        resolve([{\r\n          label: '南京',\r\n          value: 1\r\n        }, {\r\n          label: '北京',\r\n          value: 2\r\n        }, {\r\n          label: '上海',\r\n          value: 3,\r\n          disabled: true\r\n        }])\r\n      })\r\n    },\r\n    submit () {\r\n      return new Promise(resolve => {\r\n        resolve()\r\n      })\r\n    }\r\n  }\r\n}\r\n</script>\r\n"